.hero-section {
    padding: 60px 0;
    position: relative;
    z-index: 1000;
    background-color: var(--color-white);

    @media screen and (min-width:$bp-mobile) {
        padding: 150px 0 124px;
    }

    &__title-first-element {
        font-size: 15.417vw;
        white-space: nowrap;
        font-weight: 400;
        position: relative;
        margin-top: 12px;

        img {
            position: absolute;
            width: 6px;
            height: 12px;
        }

        @media screen and (min-width:$bp-mobile) {
        font-size: 8.333vw;
        font-weight: 500;
        position: relative;
        margin-top: 0;
 
        img {
            top: 10%;
            right: 0px;
            width: 12px;
            height: 21px;
        }

        span {
            color: var(--color-brand-ligth);
        }
    }
}

    &__title-second-element {
        display: flex;
        align-items: center;
        width: 61.25vw;
        height: 32.5vw;
        background: url('../../assets/DO.svg') no-repeat center;
        background-size: contain;
        margin-top: 12px;

        @media screen and (min-width:$bp-mobile) {
        margin-top: 60px;
        width: 31vw;
        height: 16.1vw;

        p {
            margin: 0;
            font-weight: 500;
            font-size: 260px;
            margin-bottom: 106px;
        }

        img {
            margin-bottom: 72px;
            width: 100%;
            height: 100%;
        }

    }
} 

    &__title-third-element {
        font-size: 8.51vw;
        white-space: nowrap;
        width: 100%;
        margin-top: 12px;

        @media screen and (min-width:$bp-mobile){
            font-size: 5.208vw;
            font-weight: 500;
            height: 100%;
            max-height: 100px;
        }
    }

    // &__DO-element {
    //     width: 27.083vw;
    //     height: 16.146vw;
    // }

    &__diveder {
        width: 100%;
        height: 100%;
        max-width: 7px;
        max-height: 16px;

        @media screen and (min-width:$bp-mobile) {
        max-height: 28px;
        max-width: 12px;
        }
    }

    &__animation-text {
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 3.333vw;
        text-transform: uppercase;
        color: var(--button-default-color);
        justify-content: flex-end;
        gap: 12px;

        @media screen and (min-width: $bp-mobile) {
        font-size: 1.146vw;
        justify-content: start;
        margin-bottom: 1.563vw;
        }

        p {
            margin: 0;
            white-space: nowrap;
        }
    }

    &__span-container {

        &:first-child {
            display: flex;
            flex-direction: column-reverse;
        }

        @media screen and (min-width:$bp-mobile) {
        &:first-child {    
        flex-direction: row;
        gap: 32px;
        }

        &:nth-child(2) {
            display: flex;
            justify-content: space-between;
        }
    }
    }

    &__text {
        display: flex;
       flex-direction: column;
        align-items: flex-end;
        font-size: 1.25vw;
        font-weight: 400;
    }

    &__text-paragraph {
        max-width: 262px;
        margin: 32px 0 16px; 
        font-size: 4.167vw;

        @media screen and (min-width:$bp-mobile) {
            max-width: 444px;
            margin: 96px 0 48px;
            font-size: 1.458vw;
        }

        span {
            font-weight: 700;
        }
    }

    &__button {
        display: flex;
        align-items: flex-end;
        width: 100%;
        margin-top: 72px;
        padding: 0;
        cursor: pointer;

        a {
            display: flex;
            width: 100%;
            justify-content: space-between;
            align-items: center;
            font-size: 5vw; 
            font-weight: 800;
            text-decoration: none;
            text-transform: uppercase;
            background: var(--color-brand-ligth);
            color: var(--color-white);
            padding: 0 0 0 15px;
            height: 64px;

            p {
                margin: 0;
            }

            path {
                fill: var(--color-white);
            }
            
            path:nth-child(3){
                fill: var(--primary-color);         
                }
            
            path:nth-child(4){
                fill: var(--primary-color);         
                }
            
            path:nth-child(5){
                fill: var(--primary-color);         
                }
        }

        @media screen and (min-width:$bp-mobile) {
        height: 5.208vw;
        margin-top: 80px;
        border-bottom: 1px solid var(--primary-color);

        a {
            font-size: 1.875vw; 
            background: transparent;
            color: var(--primary-color);
            padding: 0;
            transition: background, color, padding, .5s ease-in-out;

            svg {
                height: 3.75vw;
                width: 3.75vw;
                transition: .5s ease-in-out;
            }

            path {
                fill: var(--primary-color);
            }

            path:nth-child(3){
                fill: var(--color-brand-ligth);         
                }
            
            path:nth-child(4){
                fill: var(--color-brand-ligth);         
                }
            
            path:nth-child(5){
                fill: var(--color-brand-ligth);         
                }
            

        }

        &:hover {
            border-bottom: none;

            a {
                background: var(--color-brand-ligth);
                color: var(--color-white);
                padding: 0 0 0 15px;

                svg {
                    transform: rotate(15deg);
                    transition: transform .5s ease-in-out;
                }

                path {
                    fill: var(--color-white);
                }
                
                
                path:nth-child(3){
                    fill: var(--primary-color);         
                    }
                
                path:nth-child(4){
                    fill: var(--primary-color);         
                    }
                
                path:nth-child(5){
                    fill: var(--primary-color);         
                    }
                }
                
                &:active {
                    background-color: var(--color-brand-dark);
                    color: var(--color-white)
                }
        }
    }
    }

